import { Component, OnInit, Input } from '@angular/core';
import { NgClass } from '@angular/common';

import { ParsedInputSentenceData } from '../../../services/rb-sentence-parser.service';

@Component({
  selector: 'app-rb-annotated-sentence',
  template: `
    <span>{{index + 1}}.</span>
    <span *ngFor="let chunk of _parsedSentence" [ngClass]="[chunk.type, chunk.polarity]"> {{chunk.text}}</span>
  `,
  styleUrls: ['./rb-annotated-sentence.component.css']
})
export class RbAnnotatedSentenceComponent implements OnInit {

  _parsedSentence: ParsedInputSentenceData;

  @Input() index: number;
  @Input('parsedSentence')
  set  parsedSentence(sentence: ParsedInputSentenceData) {
    this._parsedSentence = sentence;
    this.renderSentence();
  };

  ngOnInit() {

  }

  renderSentence() {
    console.log(this._parsedSentence);
  }

}



