import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";

import * as d3 from 'd3';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit {

  codeUrl: string;
  activeLinkSVGId: string;
  constructor(private _router: Router) {
  }

  ngOnInit() {
    this.codeUrl = 'https://github.com/MMontgomeryTaggart/CV_MachineLearning';

    // Update the active link id and set the rect to be 100% for the active link.
    const currentRoute = this._router.url.split('/').pop();
    switch (currentRoute){
      case "":
        this.activeLinkSVGId = "home";
        break;
      case "vis":
        this.activeLinkSVGId = "vis";
        break;
      case "models":
        this.activeLinkSVGId = "models";
    }

    // Add the mouseOver mouseOut behaivor
    const anchors = d3.selectAll("div.nav_link_div a")
    anchors.on("mouseover", this.onLinkMouseover).on("mouseout", this.onLinkMouseout);

    // Add SVG
    setTimeout(()=>{
      // Add svg elements for link selection
      let navLinks = d3.selectAll("div.nav-link-div");
      navLinks.each(function(d, i){
        let currentDivSelection = d3.select(this);
        let currentAnchor = currentDivSelection.select("a").node() as HTMLElement;
        let currentSvgId = currentAnchor.getAttribute("data-svg-id");
        let currentBoundingRect = currentAnchor.getBoundingClientRect();
        let currentWidth = currentBoundingRect.width;
        currentDivSelection.attr("style", `width: ${currentWidth}px`);
        let svg = currentDivSelection.append("svg");
        svg.attr("width", currentWidth)
          .attr("height", "10px")
          .attr("style", "position: relative; top: 26px; fill: black;")
          .attr("id", currentSvgId);
      });
      let svg = d3.selectAll("div.nav-link-div svg");
      let rects = svg.append("rect");
      rects.attr("height", "3px")
        .attr("width", 0)
        .attr("fill", "#4F4F4F");

      this.setRectWidth(this.activeLinkSVGId, "100%");
    }, 200);
  }

  onLinkMouseover(event: any){
    const id = event.srcElement.getAttribute("data-svg-id");
    this.setRectWidth(id, "100%")
  }

  onLinkMouseout(event: any){
    if (event.srcElement.getAttribute("data-svg-id") === this.activeLinkSVGId){
      return;
    }
    const id = event.srcElement.getAttribute("data-svg-id");
    this.setRectWidth(id, "0")
  }

  onLinkClick(event: any){
    const newId = event.srcElement.getAttribute("data-svg-id");
    // Reset old link
    this.setRectWidth(this.activeLinkSVGId, "0")

    // Set new link
    this.activeLinkSVGId = newId;
    this.setRectWidth(newId, "100%")
  }

  onCodeClick(){
    this.setRectWidth("code", "0");
  }

  setRectWidth(svgId: string, width: string){
    const rect = d3.select(`svg#${svgId}`).select("rect")
    rect.transition()
      .duration(300)
      .attr("width", width);
  }

}



