import { Component, OnInit, Input, AfterViewInit } from '@angular/core';

import * as d3 from 'd3-selection';
import * as d3Shape from 'd3-shape';
import * as d3Ease from 'd3-ease';
import * as d3Interpolate from 'd3-interpolate';
import {DefaultArcObject} from 'd3-shape';

@Component({
  selector: 'app-prediction-result',
  template: ``,
  styleUrls: ['./prediction-result.component.css']
})
export class PredictionResultComponent implements OnInit, AfterViewInit {

  // D3 element types specified as per https://stackoverflow.com/questions/40756344/d3-v4-typescript-types
  @Input() myId: string;
  myContainer: d3.Selection<HTMLElement, any, any, any>;
  mySvg: d3.Selection<SVGElement, any, any, any>;
  svgWidth: number;
  svgHeight: number;
  myContentCircle: d3.Selection<SVGCircleElement, any, any, any>;
  circleGrayFill: string;
  circleGreenFill: string;
  circleRedFill: string;
  myVerticalLine: d3.Selection<SVGLineElement, any, any, any>;
  myHorizontalLine: d3.Selection<SVGLineElement, any, any, any>;
  myLoadArcGenerator: d3Shape.Arc<any, DefaultArcObject>;
  myLoadArcGroup: d3.Selection<SVGGElement, any, any, any>;
  myLoadArcPath: d3.Selection<SVGPathElement, any, any, any>;
  myDropletGroup: d3.Selection<SVGGElement, any, any, any>;
  dropletIcon: string;
  spinDuration: number;
  constructor() { }

  ngOnInit() {
    this.svgWidth = this.svgHeight = 80;
    this.circleGrayFill = '#B1B2B1'; // 'darkgray';
    this.circleGreenFill = '#38C16F';
    this.circleRedFill = '#DF504F';
    this.spinDuration = 1500;
    this.dropletIcon = `<path d="M13.84,0C11.82,16.12,0,27.46,0,35.1a13.84,13.84,0,1,0,27.67,0C27.67,27.46,15.85,16.12,13.84,0Z" />`;
  }

  ngAfterViewInit() {
    this.myContainer = d3.select(`#${this.myId}`);
    this.createContent();
    this.spinLoadArc();
  }

  createContent() {
    // We need a circle with two rects on it and a loady thingy on the edge.
    this.mySvg = this.myContainer.append<SVGElement>('svg')
      .attr('width', this.svgWidth)
      .attr('height', this.svgHeight)
      .classed('d3-result-svg', true);

    this.myContentCircle = this.mySvg.append<SVGCircleElement>('circle')
      .attr('cx', this.svgWidth / 2)
      .attr('cy', this.svgHeight / 2)
      .attr('r', 32)
      .attr('stroke-width', 5)
      .attr('stroke', this.circleGrayFill)
      .attr('fill', 'white')
      .attr('opacity', 1)
      .classed('contentCircleClass', true);

    this.myDropletGroup = this.mySvg.append<SVGGElement>('g').attr('id', 'dropletGroup');
    this.myDropletGroup.html(this.dropletIcon);
    this.myDropletGroup.attr('fill', this.circleGrayFill)
      .attr('transform', 'translate(26.5, 15)');

    this.myHorizontalLine = this.mySvg.append<SVGLineElement>('line')
      .attr('y1', .5)
      .attr('y2', .5)
      .attr('x2', 15)
      .attr('stroke', 'white')
      .attr('stroke-weight', 1)
      .attr('transform', 'translate(33, 50)')
      .attr('opacity', 0);
    this.myVerticalLine = this.mySvg.append<SVGLineElement>('line')
      .attr('x1', .5)
      .attr('x2', .5)
      .attr('y2', 15)
      .attr('stroke', 'white')
      .attr('stroke-weight', 1)
      .attr('transform', 'translate(40, 43)')
      .attr('opacity', 0);

    this.myLoadArcGenerator = d3Shape.arc()
      .innerRadius(34)
      .outerRadius(39)
      .startAngle(0)
      .endAngle(Math.PI / 3);

    this.myLoadArcGroup = this.mySvg.append<SVGGElement>('g')
      .classed('loadArcGroup', true)
      .attr('opacity', 0)
      .attr('transform', `translate(${this.svgWidth / 2}, ${this.svgHeight / 2})`);

    this.myLoadArcPath = this.myLoadArcGroup.append<SVGPathElement>('path')
      .attr('d', this.myLoadArcGenerator({
        innerRadius: 34,
        outerRadius: 39,
        startAngle: 0,
        endAngle: Math.PI / 3
      }))
      .attr('fill', '#3A6DA8');
  }

  spinLoadArc() {
    setInterval(() => {
      this.myLoadArcPath.transition()
        .ease(d3Ease.easeLinear)
        .duration(this.spinDuration)
        .attrTween('transform', () => {
          return d3Interpolate.interpolateString('rotate(0)', 'rotate(360)');
        });
    }, this.spinDuration);
  }

  showLoading() {
    this.setLoadArcOpacity(1);
    this.setCircleAndDropletColor(this.circleGrayFill);
    this.setVerticalLineOpacity(0);
    this.setHorizontalLineOpacity(0);
  }

  showResult(result: number) {
    this.setLoadArcOpacity(0);
    if (result === 1) {
      this.setCircleAndDropletColor(this.circleRedFill);
      this.setHorizontalLineOpacity(1);
      this.setVerticalLineOpacity(1);
    } else {
      this.setCircleAndDropletColor(this.circleGreenFill);
      this.setHorizontalLineOpacity(1);
    }
  }

  setLoadArcOpacity(opacity: number) {
    // debugger;
    this.myLoadArcGroup
      .transition()
      .duration(300)
      .attr('opacity', opacity);
  }

  setCircleAndDropletColor(color: string) {
    this.myContentCircle
      .transition()
      .duration(300)
      .attr('stroke', color);

    this.myDropletGroup
      .transition()
      .duration(300)
      .attr('fill', color);
  }

  setVerticalLineOpacity(opacity: number) {
    this.myVerticalLine
      .transition()
      .duration(300)
      .attr('opacity', opacity);
  }

  setHorizontalLineOpacity(opacity: number) {
    this.myHorizontalLine
      .transition()
      .duration(300)
      .attr('opacity', opacity);
  }
}



