import { Component, OnInit, Input, AfterViewInit } from '@angular/core';

import { SvmResponse, PredictionResponse, SvmExplanationRow } from '../../../services/data.service';

export interface ColumnWidths {
  term: number;
  count: number;
  firstX: number;
  idf: number;
  firstEquals: number;
  unnormalized: number;
  arrow: number;
  normalized: number;
  secondX: number;
  weight: number;
  secondEquals: number;
  score: number;
}

export interface AbsentFeatureColumnWidths {
  term: number;
  idf: number;
  weight: number;
}

interface ZeroCountRow extends SvmExplanationRow {
  featureSpanClass: 'pos' | 'neg';
}

@Component({
  selector: 'app-svm-explanation',
  template: `
    <h2>How the SVM Model Interprets the Text</h2>
    <app-explanation-text-box [content]="explanationContent"></app-explanation-text-box>
    <table id="present-features-table">
      <colgroup>
        <col width="{{columnWidths.term}}"/>
        <col width="{{columnWidths.count}}"/>
        <col width="{{columnWidths.firstX}}"/>
        <col width="{{columnWidths.idf}}"/>
        <col width="{{columnWidths.firstEquals}}"/>
        <col width="{{columnWidths.unnormalized}}"/>
        <col width="{{columnWidths.arrow}}"/>
        <col width="{{columnWidths.normalized}}"/>
        <col width="{{columnWidths.secondX}}"/>
        <col width="{{columnWidths.weight}}"/>
        <col width="{{columnWidths.secondEquals}}"/>
        <col width="{{columnWidths.score}}"/>
      </colgroup>
      <thead></thead>
      <tr>
        <td>Feature</td>
        <td>Count</td>
        <td></td>
        <td>IDF Value</td>
        <td></td>
        <td>Unnormalized Representation</td>
        <td></td>
        <td>Normalized Representation</td>
        <td></td>
        <td>Model Weight</td>
        <td></td>
        <td>Score</td>
      </tr>
      <thead></thead>
    </table>
    
    <app-svm-term-row *ngFor="let datum of rowsWithPresentTerms; let last = last"
                      [class.last-item]="last"
                      [rowData]="datum" 
                      [columnWidths]="columnWidths"></app-svm-term-row>
    
    <div id="tally-container">
      <div class="tally-flex-container">
        <div id="feature-scores-sum-line-div">+</div>
      </div>
      <div class="tally-flex-container">
        <div id="score-total-div"><span id="score-total-span">Score Total</span><span>{{scoreTotal.toFixed(2)}}</span></div>
      </div>
      <div class="tally-flex-container">
        <div id="model-bias-div"><span id="model-bias-span">Model Bias</span><span>{{data.dynamic[0].intercept.toFixed(2)}}</span></div>
      </div>
      <div class="tally-flex-container">
        <div id="final-prediction-sum-line-div">+</div>
      </div>
      <div id="final-tally-line-container">
        <div id="final-prediction-div">
          <span id="final-prediction-span">Final Prediction</span>
          <span id="final-prediction-value-container">
            <span id="final-prediction-span-value" class="{{this.finalPredictionClass}}">{{finalPrediction.toFixed(2)}}</span>
          </span>
        </div>
      </div>
    </div>
    
    <div id="zero-count-feature-header">Features not present in the text, sorted by Model Weight</div>
    <table id="zero-count-table">
      <colgroup>
        <col width="{{absentFeaturesColumnWidths.term}}"/>
        <col width="{{absentFeaturesColumnWidths.weight}}"/>
        <col width="{{absentFeaturesColumnWidths.idf}}"/>
      </colgroup>
      <thead>
        <tr>
          <th class="align-left">Feature</th>
          <th>Model Weight</th>
          <th>IDF Value</th>
        </tr>
      </thead>
      <tbody>
        <tr *ngFor="let row of rowsWithZeroCounts">
          <td class="align-left"><span class="term-span {{row.featureSpanClass}}">{{row.term}}</span></td>
          <td>{{row.weight.toFixed(2)}}</td>
          <td>{{row.idf.toFixed(2)}}</td>
        </tr>
      </tbody>
      
    </table>
  `,
  styleUrls: ['./svm-explanation.component.css']
})
export class SvmExplanationComponent implements OnInit {

  private data: SvmResponse;
  private rowsWithPresentTerms: SvmExplanationRow[];
  private rowsWithZeroCounts: ZeroCountRow[];
  private scoreTotal: number;
  private explanationContent: string = this.getExplanationContent();
  private tableWidth: number;
  private finalPrediction: number;
  private finalPredictionClass: 'pos' | 'neg';
  private columnWidths: ColumnWidths = {
    term: 200,
    count: 100,
    firstX: 20,
    idf: 110,
    firstEquals: 20,
    unnormalized: 110,
    arrow: 100,
    normalized: 110,
    secondX: 20,
    weight: 110,
    secondEquals: 20,
    score: 110
  };
  private absentFeaturesColumnWidths: AbsentFeatureColumnWidths = {
    term: 330,
    weight: 300,
    idf: 250
  }

  constructor() {}

  ngOnInit() {
    // Calculate the table width for use in positioning the tallies.
    let tableWidthSum = 0;
    for (let columnInfo in this.columnWidths) {
      if (this.columnWidths.hasOwnProperty(columnInfo)){
        const width = this.columnWidths[columnInfo];
        tableWidthSum += width;
      }
    }
    this.tableWidth = tableWidthSum;
  }

  // Type assertion is necessary becasue call signature must match rb-explanation as per
  // https://github.com/Microsoft/TypeScript/issues/7294
  setData(data: PredictionResponse): void {
    this.data = data as SvmResponse;
    // Filter the rows to only rows with counts >= 1. All the other rows will be displayed below the sum.
    this.rowsWithPresentTerms = this.data.dynamic.filter((row: SvmExplanationRow, index, array) => {
      return row.count !== 0;
    });
    let sum = 0;
    for (let row of this.rowsWithPresentTerms) {
      sum += row.modelScore;
    }
    this.scoreTotal = sum;
    this.rowsWithZeroCounts = []
    for (let row of data.dynamic) {
      if (row.count === 0) {
        let zeroRow = {
          term: row.term,
          weight: row.weight,
          idf: row.idf,
          count: row.count,
          unnormalized: row.unnormalized,
          normalization: row.normalization,
          modelScore: row.modelScore,
          intercept: row.intercept,
          featureSpanClass: row.weight > 0 ? 'pos' : 'neg'
        }
        this.rowsWithZeroCounts.push(zeroRow as ZeroCountRow)
      }
      this.finalPrediction = data.dynamic[0].intercept + this.scoreTotal;
      this.finalPredictionClass = this.finalPrediction > 0 ? 'pos' : 'neg';
    }

    // Sort both collections by model score.
    this.rowsWithPresentTerms.sort((a, b) => {
      return b.weight - a.weight;
    })
    this.rowsWithZeroCounts.sort((a, b) =>{
      return b.weight - a.weight;
    })
  }

  getData(): SvmResponse {
    return this.data;
  }

  getExplanationContent(): string {
    return `<p>The support vector machines that we trained are linear models. This means that they learn a weight for each
    dimension of their input. In this case there are 100 input dimensions, each representing a word or phrase that
    the model selected from the training corpus. In machine learning, each input dimension is referred to as a "feature".
    The features selected by the model can be seen in the list below. Features that were assigned a positive weight are 
    highlighted in orange and features that were assigned a negative weight are highlighted in green.</p>
    <br>
    <p>To help the model select useful features we employed a technique known as term-frequency inverse-document-frequency 
    (TFIDF) which assigns weights to all the words in the corpus. Words that are judged to be more salient to the meaning
    of a note are given a higher score than words which are judged to be less salient. So in addition to a model weight
    for each feature you will also see an IDF weight.</p><br>
    <p>To arrive at the final prediction the model counts how many times each feature appears in the note 
    (the "Count" column) and multiplies each count by its respective IDF score (the "IDF Value" column). This yields a list with 100 entries,
    one for each feature. This list can be thought of as a 100-dimensional vector embedded in 100-dimensional space
    (the "Unnormalized Representation" column. In
    order to make the model less sensitive to note length, this vector is then normalized. That is, each entry in the vector
    is divided by the sum of all the squared entries in the vector. This effectively shrinks the vector so that it has a length
    of 1. The normalized values can be seen in the "Normalized Representation Column". Next, the normalized values are
    each multiplied by the weights that were learned by the model (the "Weights" column) to give a score for each feature
    (the "Score" column). All the scores are summed and the model bias is added to yield the final prediction. Prediction
    values above 0 are interpreted as a positive prediction and values below zero are interpreted as a negative 
    prediction.</p><br>
    <p>It is common in the machine learning domain to consider the geometric interpretation of a model in order to gain
    some intuition about how it works. Geometrically, the features that are fed to a machine learning model can be thought
    of as points in space. Ideally, features for a model are chosen such that similar things lie close together in that space 
    and dissimilar things lie far apart. Most 
    if not all machine learning classifiers, such as these, can be thought of as tools for learning the boundaries in 
    these spaces that separate groups of points from each other. In the case of linear classifiers, these
    boundaries are flat surfaces, more formally known as hyperplanes. The boundaries need not be flat, however, and
    many models exist to draw curved, non-linear boundaries, such as the extra trees and neural network models that
    were also used in this project.</p>
    
    `;
  }
}



