import { Component, OnInit, Input, AfterViewInit } from '@angular/core';

import { PredictionResponse, SvmResponse } from '../../../services/data.service';

@Component({
  selector: 'app-cnn-explanation',
  template: `
    <p>Convolutional Neural Networks are a sophisticated model that is commonly used in computer vision. Neural networks 
      in general are composed of several layers of computational units referred to as neurons. The mechanics of neural 
      networks are outside the scope of this explanation, however, an attempt will be made to explain the intuition 
      behind convolutional neural networks as used in NLP.<p><br>
      <p>Unlike the SVM and ET models, the CNN model uses a representation of the text that utilizes word embeddings. 
        Word embeddings are a large collection of words, usually gathered from the internet, where each word has been 
        paired with a vector that serves as a representation of that words meaning. Thinking of each vector as a point 
        is space where its corresponding word can be found, high-quality word embeddings will place words that have 
        similar meanings near each other in space. By using word embeddings rather than words themselves the neural 
        network is able to recognize words with similar meanings as being similar.<p><br>
      <p>To process a note, each word must be converted to its respective word embedding and then all the embeddings 
        are stacked side-by-side in a long row. It may be helpful to imagine this row of embeddings as a long, horizontal rectangle of 
        information, not unlike a long rectangular image. Using the analogy of an image, each word is a thin vertical 
        stripe in the image. You can imagine that it might be possible to identify patterns in this image where phrases 
        that have similar meaning will manifest themselves as sections in the image with similar banding. It is the job 
        of a convolutional neural networks to identify these patterns. It does so by learning what people refer to as 
        filters and then convolving these filters with the image. Hence the name, convolutional neural network. 
        Using our image analogy, the process of convolving image filters is analogous to holding a small image in your 
        hand whose meaning is significant and moving it around the bigger image to see where it might be found in the 
        bigger image. For example, imagine you are looking at a photo of a graduating class and you hold in your hand 
        a small photo of the face of someone you wish to find in that class. You might move the small photo around the 
        larger photo comparing the faces in the large photo to the picture you have in your hand to evaluate if you 
        had found a match. In a similar way, the CNN identifies snippets of our word embedding image that are significant 
        and then compares them to incoming images to draw conclusions about the incoming data. </p><br>
    <p>Given the sophistication of these models their performance will be poor unless they are trained on large 
      datasets. The small size of the training dataset produced for this project may be the main reason why the neural 
      network models performed so poorly. </p>
  `,
  styleUrls: ['./cnn-explanation.component.css']
})
export class CnnExplanationComponent implements OnInit {

  private data: SvmResponse;
  private explanationContent: string = this.getExplanationContent();

  constructor() {}

  ngOnInit() {}

  setData(data: PredictionResponse): void {
    this.data = data as SvmResponse;
  }

  // keep this method to be compatible with the way the other explanations are instantiated.
  getData(): SvmResponse {
    return this.data;
  }

  getExplanationContent(): string {
    return `<p>The support vector machines that we trained are linear models. This means that they learn a weight for each
    dimension of their input. In this case there are 100 input dimensions, each representing a word or phrase that
    the model selected from the training corpus. In machine learning, each input dimension is referred to as a "feature".
    The features selected by the model can be seen in the list below. Features that were assigned a positive weight are 
    highlighted in orange and features that were assigned a negative weight are highlighted in green.</p>
    <br>
    <p>To help the model select useful features we employed a technique known as term-frequency inverse-document-frequency 
    (TFIDF) which assigns weights to all the words in the corpus. Words that are judged to be more salient to the meaning
    of a note are given a higher score than words which are judged to be less salient. So in addition to a model weight
    for each feature you will also see an IDF weight.</p><br>
    <p>To arrive at the final prediction the model counts how many times each feature appears in the note 
    (the "Count" column) and multiplies each count by its respective IDF score (the "IDF Value" column). This yields a list with 100 entries,
    one for each feature. This list can be thought of as a 100-dimensional vector embedded in 100-dimensional space
    (the "Unnormalized Representation" column. In
    order to make the model less sensitive to note length, this vector is then normalized. That is, each entry in the vector
    is divided by the sum of all the squared entries in the vector. This effectively shrinks the vector so that it has a length
    of 1. The normalized values can be seen in the "Normalized Representation Column". Next, the normalized values are
    each multiplied by the weights that were learned by the model (the "Weights" column) to give a score for each feature
    (the "Score" column). All the scores are summed and the model bias is added to yield the final prediction. Prediction
    values above 0 are interpreted as a positive prediction and values below zero are interpreted as a negative 
    prediction.</p><br>
    <p>It is common in the machine learning domain to consider the geometric interpretation of a model in order to gain
    some intuition about how it works. Geometrically, the features that are fed to a machine learning model can be thought
    of as points in space. Ideally, features for a model are chosen such that similar things lie close together in that space 
    and dissimilar things lie far apart. Most 
    if not all machine learning classifiers, such as these, can be thought of as tools for learning the boundaries in 
    these spaces that separate groups of points from each other. In the case of linear classifiers, these
    boundaries are flat surfaces, more formally known as hyperplanes. The boundaries need not be flat, however, and
    many models exist to draw curved, non-linear boundaries, such as the extra trees and neural network models that
    were also used in this project.</p>
    
    `;
  }
}



