import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ContentModule } from './modules/content/content.module';

import { Router } from '@angular/router';

import { AppComponent } from './app.component';
import { LandingComponent } from './modules/landing/landing.component';
import { ExplanationParentComponent } from './modules/content/model-explanations/explanation-parent.component';
import { RbExplanationComponent } from './modules/content/model-explanations/rb-explanation.component';
import { SvmExplanationComponent } from './modules/content/model-explanations/svm-explanation.component';
import { EtExplanationComponent } from './modules/content/model-explanations/et-explanation.component';
import { CnnExplanationComponent } from './modules/content/model-explanations/cnn-explanation.component';

import { ConfigurationService } from './configuration.service';
import { DataService } from './services/data.service';

import { AppRoutingModule } from './app-routing.module';
import { HttpClientModule } from '@angular/common/http';


@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    AppRoutingModule,
    BrowserModule,
    ContentModule,
    HttpClientModule
  ],
  providers: [
    ConfigurationService,
    DataService
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    RbExplanationComponent,
    SvmExplanationComponent,
    EtExplanationComponent,
    CnnExplanationComponent
  ]
})
export class AppModule { }



