import {Component, OnInit, Input, AfterViewInit, ElementRef} from '@angular/core';

import { SvmExplanationRow } from '../../../services/data.service';
import { ColumnWidths } from './svm-explanation.component';

@Component({
  selector: 'app-svm-term-row',
  template: `
    <table>
      <colgroup>
        <col width="{{columnWidths.term}}"/>
        <col width="{{columnWidths.count}}"/>
        <col width="{{columnWidths.firstX}}"/>
        <col width="{{columnWidths.idf}}"/>
        <col width="{{columnWidths.firstEquals}}"/>
        <col width="{{columnWidths.unnormalized}}"/>
        <col width="{{columnWidths.arrow}}"/>
        <col width="{{columnWidths.normalized}}"/>
        <col width="{{columnWidths.secondX}}"/>
        <col width="{{columnWidths.weight}}"/>
        <col width="{{columnWidths.secondEquals}}"/>
        <col width="{{columnWidths.score}}"/>
      </colgroup>
      <thead></thead>
      <tbody>
        <tr>
          <td class="td-align-right">
            <span class="term-span {{termSpanClass}}">{{_rowData.term}}</span>
          </td>
          <td>
            <span class="count-span">{{_rowData.count}}</span>
          </td>
          <td>
            <span class="first-x-span">x</span>
          </td>
          <td>
            <span class="idf-span">{{_rowData.idf.toFixed(2)}}</span>
          </td>
          <td>
            <span class="first-equals-span">=</span>
          </td>
          <td>
            <span class="unnormalized-span">{{_rowData.unnormalized.toFixed(2)}}</span>
          </td>
          <td>
            <span class="arrow-span">(Arrow)</span>
          </td>
          <td>
            <span class="normalized-span">{{_rowData.normalization.toFixed(2)}}</span>
          </td>
          <td>
            <span class="second-x-span">x</span>
          </td>
          <td>
            <span class="weight-span">{{_rowData.weight.toFixed(2)}}</span>
          </td>
          <td>
            <span class="second-equals-span">=</span>
          </td>
          <td>
            <span class="score-span">{{_rowData.modelScore.toFixed(2)}}</span>
          </td>
          <td>
        </tr>
        <tr *ngIf="!isLastRow">
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td>
            <span class="right-plus-span">+</span>
          </td>
        </tr>
      <tbody>
    </table>
  `,
  styleUrls: ['./svm-term-row.component.css']
})
export class SvmTermRowComponent implements OnInit {

  private _rowData: SvmExplanationRow;
  private termSpanClass: string;
  private isLastRow = false;
  @Input()
  set rowData (rowData: SvmExplanationRow) {
    if (rowData.weight > 0) {
      this.termSpanClass = 'pos';
    } else {
      this.termSpanClass = 'neg';
    }
    this._rowData = rowData;
  }
  @Input() columnWidths: ColumnWidths;

  constructor(private thisElement: ElementRef) {}

  ngOnInit() {
    setTimeout(() => {
      const classList = this.thisElement.nativeElement.classList;
      if (classList.contains('last-item')) {
        this.isLastRow = true;
      }
    });
  }

}


