import { Component, OnInit, Input, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';

import {DataService, Target, Modifier, TargetsAndModifiers, RbResponse, PredictionResponse} from '../../../services/data.service';
import { RbSentenceParserService, PlainTextChunk,
  TargetTextChunk, ModifierTextChunk, ParsedInputSentenceData } from '../../../services/rb-sentence-parser.service';

import { RbAnnotatedSentenceComponent } from './rb-annotated-sentence.component';

import {isUndefined} from 'util';

@Component({
  // encapsulation: ViewEncapsulation.None,
  selector: 'app-rb-explanation',
  template: `
    <h2>Sentences Processed by the Rule-based Algorithm</h2>
    <app-explanation-text-box [content]="explanationContent"></app-explanation-text-box>
    <app-rb-annotated-sentence *ngFor="let parsedSentenceData of parsedInputSentenceData; let i = index" [index]="i" [parsedSentence]="parsedSentenceData">
    </app-rb-annotated-sentence>
    
  `,
  styleUrls: ['./rb-explanation.component.css']
})
export class RbExplanationComponent implements OnInit {

  data: RbResponse;
  private targetsAndModifiers: TargetsAndModifiers;
  stringifiedData: string;
  explanationContent: string = this.getExplanationContent();
  parsedInputSentenceData: ParsedInputSentenceData;

  constructor(private sentenceParserService: RbSentenceParserService, private dataService: DataService) {}

  ngOnInit() {
    // debugger;
    this.dataService.getTargetsAndModifiers((data: TargetsAndModifiers) => {
      // debugger;
      this.targetsAndModifiers = data;
      this.getParsedInputSentenceData();
    });
  }

  // Type assertion is necessary becasue call signature must match svm-explanation as per
  // https://github.com/Microsoft/TypeScript/issues/7294
  setData(data: PredictionResponse): void {
    // debugger;
    this.data = data as RbResponse;
    this.stringifiedData = JSON.stringify(data);
    console.log(data);
    this.getParsedInputSentenceData();
  }

  getData(): RbResponse {
    return this.data;
  }

  private getParsedInputSentenceData() {
    // Since we need the targets and modifiers, and the prediction data, and they come from different places, both async,
    // this function acts as a gate to ensure that we have both before we call the parserService.
    // debugger;
    if (isUndefined(this.targetsAndModifiers) || isUndefined(this.data)) {
      return;
    }
    this.parsedInputSentenceData = this.sentenceParserService.parseInputSentences(this.data.dynamic, this.targetsAndModifiers);
  }

  renderVisualizationContents() {}


  getExplanationContent() {
    return `
    <p>The rule-based classifier relies on two lists of words: a list of targets and a list of modifiers. Target words 
    are words like bleed, hematoma, or sdh. They are the keywords that most directly indicate that a bleeding 
    event has occurred. Modifier words are words like history of, denies, or presents with. They are words or 
    phrases that modify the meaning of the target words. Each word in the list of modifiers is designated as either 
    a positive modifier, or a negating modifier. For example, one negative modifier that is included in the modifier 
    list is no evidence. Consider the text there is no evidence of GI bleed. The target in this sentence is 
    bleed. However, since the negative modifier no evidence appears in the same sentence the algorithm will not 
    mark the sentence as an indication of bleeding. Contrast this example with another: chief complaint: epistaxis. 
    In this case the rule-based algorithm will detect that the positive modifier chief complaint is present in the 
    same sentence as the target epistaxis and mark the sentence as an indication of bleeding. Similarly, sentences 
    with a target term but no modifier terms will also be marked as an indication of bleeding.</p>
    <br>
    <p>In the visualization below, target terms are highlighted in red. Any modifier terms that occur in the same sentence 
    are highlighted in green and are accompanied by an indicator that specifies whether they are a positive or negative modifier.</p>
    `;
  }
}


// Here is a sentence about bleeding. And another evidence of hematoma.
// Definitely not a hematoma.



