import {
  Component, OnInit, Input, AfterViewInit, OnDestroy, ViewChild, ViewChildren,
  ComponentFactoryResolver, ViewEncapsulation, OnChanges, SimpleChanges
} from '@angular/core';
import { Subscription } from 'rxjs/Subscription';

import { DataService, Target, Modifier, TargetsAndModifiers,
  PredictionResponse, RbResponse, SvmResponse} from '../../../services/data.service';
import { RbExplanationComponent } from './rb-explanation.component';
import { SvmExplanationComponent } from './svm-explanation.component';
import { EtExplanationComponent } from './et-explanation.component';
import { CnnExplanationComponent } from './cnn-explanation.component';

import { ExplanationViewDirective } from './explanation-view.directive';

import * as d3 from 'd3-selection';
import {isUndefined} from 'util';
import {isNewline} from 'codelyzer/angular/styles/cssLexer';


@Component({
  encapsulation: ViewEncapsulation.None,
  selector: 'app-explanation-parent',
  template: `
    <h1 id="explanation-header">How do they work?</h1>
    <div id="parent-nav-div">
      <div id="rule-based-tab" class="explanation-tab deactivated" data-model-name="rb" (click)="explanationSelected($event)">
        Rule-Based
      </div>
      <div id="svm-fs-tab" class="explanation-tab deactivated" data-model-name="svm-fs" (click)="explanationSelected($event)">
        SVM-FS
      </div>
      <div id="svm-ds-tab" class="explanation-tab deactivated" data-model-name="svm-ds" (click)="explanationSelected($event)">
        SVM-DS
      </div>
      <div id="et-tab" class="explanation-tab deactivated" data-model-name="et" (click)="explanationSelected($event)">
        ET
      </div>
      <div id="cnn-tab" class="explanation-tab deactivated" data-model-name="cnn" (click)="explanationSelected($event)">
        CNN
      </div>
    </div>
    <ng-template appExplanationView></ng-template>
  `,
  styleUrls: ['./explanation-parent.component.css']
})
export class ExplanationParentComponent implements OnInit, AfterViewInit, OnChanges {

  private selectedModelName: string = '';
  private activeComponent: RbExplanationComponent | SvmExplanationComponent | EtExplanationComponent | CnnExplanationComponent;
  private modelNames = ['rb', 'svm-ds', 'svm-fs']
  private changeKeyNames: string[] = ['rbData', 'svmDSData', 'svmFSData'];
  @ViewChild(ExplanationViewDirective) explanationViewProperty: ExplanationViewDirective;
  @Input() rbData: RbResponse;
  @Input() svmFSData: SvmResponse;
  @Input() svmDSData: SvmResponse;
  @Input() predictionMade: boolean;

  modelNameMap: object = {
    'rb': {data: this.rbData, tabName: 'rule-based-tab'},
    'svm-ds': {data: this.svmDSData, tabName: 'svm-ds-tab'},
    'svm-fs': {data: this.svmFSData, tabName: 'svm-fd-tab'}
  }

  constructor(private dataService: DataService, private componentFactoryResolver: ComponentFactoryResolver) {}

  ngOnInit() {
  }

  ngAfterViewInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
    // Check for changes to 'predictionMade'
    if (changes.hasOwnProperty('predictionMade') && this.inputValueIsBeingUpdated('predictionMade', changes)){
      const predictionChange = changes.predictionMade;
      if (predictionChange.previousValue === false && predictionChange.currentValue === true){
        // Remove 'deactivated' class from the tabs
        // Check if 'selectedModel' has a value, if so call explanationSelected() and pass in
        // object whose id is equal to the selected tab. Otherwise call explanationSelected()
        // with the svm tab id.
        let explanationTabs = d3.selectAll('.explanation-tab').classed('deactivated', false);
        let ruleBasedTab = d3.select('#rule-based-tab').node();
        let selectionInput = {target: ruleBasedTab}
        this.explanationSelected(selectionInput);
      }
    }

    // Check for changes to model data and update properties accordingly.
    for (let modelName of this.changeKeyNames) {
      if (changes.hasOwnProperty(modelName)){
        if (this.inputValueIsBeingSet(modelName, changes) || this.inputValueIsBeingUpdated(modelName, changes)) {
          this.onModelDataChange(modelName, changes[modelName].currentValue);
        }
      }
    }
  }

  explanationSelected(event) {
    if (event.target.classList.contains('deactivated')) {
      return;
    }
    let explanationTabs = d3.selectAll(".explanation-tab").classed('active', false);
    let selectedTabId = event.target.id;
    let selectedModelName = this.selectedModelName = event.target.getAttribute('data-model-name');
    let selectedTabElement = d3.select(`#${selectedTabId}`).classed('active', true);
    this.displayExplanation(selectedModelName);
  }

  displayExplanation(explanationName: 'rb' | 'svm-fs' | 'svm-ds' | 'et' | 'cnn') {

    let selectedComponent: any;
    let data: RbResponse | SvmResponse;

    if (explanationName === 'rb'){
      selectedComponent = RbExplanationComponent;
      data = this.rbData;
    }else if (explanationName.startsWith('svm')) {
      selectedComponent = SvmExplanationComponent;
      if (explanationName === 'svm-fs') {
        data = this.svmFSData;
      } else {
        data = this.svmDSData;
      }
    }else if (explanationName === 'et') {
      selectedComponent = EtExplanationComponent;
      data = this.svmDSData; // Just use the svm data to satisfy the compiler, this will obviously need to be changed.
    }else {
      // CNN Selected
      selectedComponent = CnnExplanationComponent;
      data = this.svmDSData; // Again, just use the svm data for now.
    }

    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(selectedComponent);
    let viewContainerRef = this.explanationViewProperty.viewContainerRef;
    viewContainerRef.clear();

    let componentRef = viewContainerRef.createComponent(componentFactory);
    let componentInstance: RbExplanationComponent | SvmExplanationComponent | EtExplanationComponent | CnnExplanationComponent
      = componentRef.instance as RbExplanationComponent | SvmExplanationComponent | EtExplanationComponent | CnnExplanationComponent;
    componentInstance.setData(data);
    this.activeComponent = componentInstance;
  }

  onModelDataChange(modelName: string, newModelData: any) {
    // update the data property of whichever view is currently being shown.
    // If no view is being shown call setTimeout and try again after the view is displayed.
    if (newModelData.model !== this.activeComponent.getData().model){
      return;
    }
    if (JSON.stringify(this.activeComponent.getData()) === JSON.stringify(newModelData)) {
      return;
    }
    this.activeComponent.setData(newModelData);
  }

  inputValueIsBeingSet(key: string, changes: SimpleChanges) {
    if (!(changes.hasOwnProperty(key))) {
      throw Error(`Changes does not have key: ${key}`)
    }
    const change = changes[key];
    if (isUndefined(change.previousValue) && !isUndefined(change.currentValue)) {
      return true;
    } else {
      return false;
    }
  }

  inputValueIsBeingUpdated(key: string, changes: SimpleChanges) {
    if (!(changes.hasOwnProperty(key))) {
      throw Error(`Changes does not have key: ${key}`)
    }
    const change = changes[key];
    if (!isUndefined(change.previousValue)) {
      return true;
    } else {
      return false;
    }
  }

}



