import {Component, OnInit, ViewChild} from '@angular/core';
import { ExplanationParentComponent } from './model-explanations/explanation-parent.component';
import { RbExplanationComponent} from './model-explanations/rb-explanation.component';
import { SvmExplanationComponent } from './model-explanations/svm-explanation.component';

import { ConfigurationService } from '../../configuration.service';
import { PredictionResponse, RbResponse, SvmResponse } from '../../services/data.service';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import * as d3 from 'd3';

interface ChildMap {
  rb: object;
  svm: object;
  'svm-ds': object;
  et: object;
  'et-ds': object;
  cnn: object;
  'cnn-ds': object;
}

@Component({
  selector: 'app-models',
  template: `
    <section id="content">
      <div id="text-entry-div">
        <div id="result-icon-div">
          <div id="rb-result-div" class="result-row">
            <div class="individual-result-div">
              <h2 style="margin: 9px;">RB</h2>
              <app-prediction-result #rbResult [myId]="'rb'" [attr.id]="'rb'"></app-prediction-result>
            </div>
          </div>
          <div id="dividing-line-div" class="result-row">
            <svg width="5" height="100%">
              <rect width="100%" height="100%" fill="#575757"></rect>
            </svg>
          </div>
          <div id="ml-results-div" class="result-row">
            <div class="paired-result-div">
              <h2>SVM</h2>
              <div class="individual-result-div">
                <h3>FS</h3>
                <app-prediction-result #svmResult [myId]="'svm'" [attr.id]="'svm'"></app-prediction-result>
              </div>
              <div class="individual-result-div">
                <h3>DS</h3>
                <app-prediction-result #svmDSResult [myId]="'svm-ds'" [attr.id]="'svm-ds'"></app-prediction-result>
              </div>
            </div>
            <div class="paired-result-div">
              <h2>ET</h2>
              <div class="individual-result-div">
                <h3>FS</h3>
                <app-prediction-result #etResult [myId]="'et'" [attr.id]="'et'"></app-prediction-result>
              </div>
              <div class="individual-result-div">
                <h3>DS</h3>
                <app-prediction-result #etDSResult [myId]="'et-ds'" [attr.id]="'et-ds'"></app-prediction-result>
              </div>
            </div>
            <div class="paired-result-div">
              <h2>CNN</h2>
              <div class="individual-result-div">
                <h3>FS</h3>
                <app-prediction-result #cnnResult [myId]="'cnn'" [attr.id]="'cnn'"></app-prediction-result>
              </div>
              <div class="individual-result-div">
                <h3>DS</h3>
                <app-prediction-result #cnnDSResult [myId]="'cnn-ds'" [attr.id]="'cnn-ds'"></app-prediction-result>
              </div>
            </div>
          </div>
        </div>
        <div id="text-area-div">
          <textarea id="text-entry" spellcheck="false" [placeholder]="textAreaPlaceholder" (input)="userText=$event.target.value"></textarea>
        </div>
        <div id="button-container-div">
          <div id="button-position-div">
            <button type="button" id="clear-button" (click)="clearText()">Clear</button>
            <button type="button" id="predict-button" (click)="predictText()">Predict</button>
          </div>
        </div>
      </div>
      <div id="examples-div">
        <h1>Try an example.</h1>
        <div id="example-selection-container">
          <div id="positive-example-div">
            <svg id="positive-selection" width="32" height="32" class="example-svg" (click)="exampledSelected($event)">
              <circle id="positive-selection" r="16" cx="16" cy="16" fill="darkgray" class="example-circle active" data-circle-index="0"></circle>
              <line y1=".5" y2=".5" x2="13" transform="translate(10, 15)" stroke="white" stroke-width="1"></line>
              <line x1=".5" x2=".5" y2="13" transform="translate(16, 9)" stroke="white" stroke-width="1"></line>
            </svg>
          </div>
          <div id="negative-example-div" class="example-svg">
            <svg id="negative-selection" width="32" height="32" class="example-svg" (click)="exampledSelected($event)">
              <circle id="negative-selection" r="16" cx="16" cy="16" fill="darkgray" class="example-circle" data-circle-index="1"></circle>
              <line y1=".5" y2=".5" x2="13" transform="translate(10, 15)" stroke="white" stroke-width="1"></line>
            </svg>
          </div>
        </div>
        <textarea id="note-display" spellcheck="false" readonly="true" [value]="activeExampleNote"></textarea>
        <div id="copy-button-div" (click)="copyExampleNote()">
          <div id="copy-svg-div">
            <svg width="25px" height="35px">
              <g transform="translate(0, 4)">
                <polyline id="left-arrow" points="9.71 18.49 1.37 9.59 9.71 0.68"/>
              </g>
            </svg>
          </div>
          <h2 id="example-button-header">Use This Example</h2>
        </div>
      </div>
    </section>
    <app-explanation-parent [predictionMade]="predictionMade" [rbData]="rbData" [svmDSData]="svmDSData" [svmFSData]="svmFSData"></app-explanation-parent>
    
  `,
  styleUrls: ['./models.component.css']
})
export class ModelsComponent implements OnInit {

  userText: string;
  textAreaPlaceholder: string;
  modelNames: string[];
  backendUrl: string;
  exampleNotes: string[];
  activeExampleNote: string;
  predictionMade: boolean = false;
  rbData: RbResponse;
  svmFSData: SvmResponse;
  svmDSData: SvmResponse;
  predictHeaders: HttpHeaders = new HttpHeaders({'Content-Type' : 'application/json'})

  @ViewChild('rbResult') rbResult: object;
  @ViewChild('svmResult') svmResult: object;
  @ViewChild('svmDSResult') svmDSResult: object;
  @ViewChild('etResult') etResult: object;
  @ViewChild('etDSResult') etDSResult: object;
  @ViewChild('cnnResult') cnnResult: object;
  @ViewChild('cnnDSResult') cnnDSResult: object;

  childMap: ChildMap;

  constructor(private http: HttpClient, private configService: ConfigurationService) {
    this.backendUrl = this.configService.getActiveUrl();
  }

  ngOnInit() {
    this.textAreaPlaceholder = 'Enter the text you want to classify.';
    this.modelNames = ['rb', 'svm', 'svm-ds', 'et', 'et-ds', 'cnn', 'cnn-ds'];
    this.childMap = {
      rb: this.rbResult,
      svm: this.svmResult,
      'svm-ds': this.svmDSResult,
      et: this.etResult,
      'et-ds': this.etDSResult,
      cnn: this.cnnResult,
      'cnn-ds': this.cnnDSResult
    };
    this.exampleNotes = this.loadNotes();
    this.activeExampleNote = this.exampleNotes[0];
  }

  predictText() {
    if (this.userText === '') {
      return;
    }
    for (const modelName of this.modelNames) {
      for (const childViewKey in this.childMap) {
        if (this.childMap.hasOwnProperty(childViewKey)) {
          const childView = this.childMap[childViewKey];
          childView.showLoading();
        }
      }
      this.http.post(this.backendUrl + '/predict',
        {text: this.userText, model: modelName},
        {headers: this.predictHeaders}
      ).subscribe((response: PredictionResponse) => {
        const childView = this.childMap[response.model];
        childView.showResult(response.prediction);

        // These values are all being watched by explanation parent.
        if (!(this.predictionMade)) {
          this.predictionMade = true;
        }

        if (response.model === 'rb') {
          this.rbData = response as RbResponse;
        } else if (response.model === 'svm') {
          this.svmFSData = response as SvmResponse;
        } else if (response.model === 'svm-ds') {
          this.svmDSData = response as SvmResponse;
        }
      },
        (error) => {
          console.log('There was an error making the prediction, logging error:')
          console.log(error);
        });
    }
  }

  clearText() {
    const textArea = document.getElementById('text-entry') as HTMLTextAreaElement;
    textArea.value = this.userText = '';
  }

  exampledSelected(event) {
    const selectedId = event.srcElement.getAttribute('id');
    const svg = d3.selectAll("circle.example-circle").classed('active', false);
    const selectedCircle = d3.select(`circle#${selectedId}`).classed('active', true);
    const domElement = selectedCircle.node() as HTMLElement;
    const index = domElement.getAttribute('data-circle-index');
    this.activeExampleNote = this.exampleNotes[index];
  }

  copyExampleNote() {
    const noteCopy = this.activeExampleNote.slice(0);
    const textArea = document.getElementById('text-entry') as HTMLTextAreaElement;
    textArea.value = this.userText = noteCopy;
  }

  loadNotes() {
    const notes = [];
    notes[0] = 'Admission Date: 04/24/2004      Discharge Date: 04/27/2004\n' +
      'Service: MEDICINE\n' +
      'Allergies: No known allergies\n' +
      '\n' +
      'Chief Complaint: chest pain, SOB, Afib\n' +
      '\n' +
      'Major Surgical or Invasive Procedure:\n' +
      'Cardiac catheterization\n' +
      'Pulmonary vein isolation\n' +
      '\n' +
      'Brief Hospital Course:\n' +
      'The patient is a 67 year old male on coumadin who presents with chest ' +
      'pain and elevated troponin. He went in and out of rapid afib and sinus ' +
      'bradycardia while admitted. Was scheduled for a PCI on 4/26/2004 which ' +
      'was complicated by orophanyngeal bleeding from TEE probe. He was ' +
      'subsequently transferred to the CCU. Nodal agents were held given his ' +
      'bradychardia when not in AFIB. A CT scan was done which showed no evidence of sdh.\n' +
      '\n' +
      'Oropharyngeal Injury:\n' +
      'Patient suffered an injury during intubation in preparation for PCI. ' +
      'Higher than normal resistance was met in the pharynx during the placement ' +
      'of the probe. Insertion of the probe was stopped. Mild oozing was noted and ' +
      'the pharynx was packed, successfully ending the oozing.\n' +
      '\n' +
      'GERD:\n' +
      'Will continue omeprazole at home, 40mg q.d. Notably the reflux symptoms only ' +
      'appear when the patient is in afib, suggesting that it may be a symptomatic ' +
      'arrythmia rather than GERD.'



    notes[1] = 'Example note 2.'

    return notes;
  }

}



