import { Injectable } from '@angular/core';
import { ConfigurationService } from '../configuration.service';

import 'rxjs/add/operator/map';

import * as d3Fetch from 'd3-fetch';
import {isUndefined} from "util";
import {DSVRowString} from 'd3-dsv';

export interface SvmExplanationRow {
  term: string;
  weight: number;
  idf: number;
  count: number;
  unnormalized: number;
  normalization: number;
  modelScore: number;
  intercept: number;
}

export interface PredictionResponse {
  prediction: number;
  time: number;
  model: string;
  dynamic: any;
}

export interface RbResponse extends PredictionResponse {
  dynamic: string[];
}

export interface SvmResponse extends PredictionResponse {
  dynamic: SvmExplanationRow[];
}

interface RawTargetOrModifier {
  Lex: string;
  Type: string;
  Regex: string;
  Direction: string;
}

export interface Target {
  name: string;
  type: string;
  regex: string;
  direction: 'backward' | 'forward' | 'bidirectional';
  id: number;
}

export interface Modifier {
  name: string;
  type: string;
  regex: string;
  direction: 'backward' | 'forward' | 'bidirectional';
  id: number;
}

export interface TargetsAndModifiers {
  targets: Target[];
  modifiers: Modifier[];
}

@Injectable()
export class DataService {

  private idCount = 0;
  private baseUrl: string;
  private targetsAndModifiersData: TargetsAndModifiers;


  constructor(private configService: ConfigurationService) {}

  processObject(rawRow: DSVRowString, index: number, column: string[]) {
    // debugger;
    const processedObject: Target = {
      name: rawRow.Lex as string,
      type: rawRow.Type as string,
      regex: rawRow.Regex as string,
      direction: rawRow.Direction as 'backward' | 'forward' | 'bidirectional',
      id: this.idCount++ as number
    }
    return processedObject;
  }

  getTargetsAndModifiers(callback: (data: TargetsAndModifiers) => void) {
    this.baseUrl = this.configService.getActiveUrl();

    const tempTargetsAndModifiers: TargetsAndModifiers = {targets: [], modifiers: []};
      if (isUndefined(this.targetsAndModifiersData)) {
        d3Fetch.tsv<Target>(this.baseUrl + '/public/data/targets.tsv', {}, this.processObject.bind(this))
          .then((value) => {
            tempTargetsAndModifiers.targets = value as Target[];

            d3Fetch.tsv<Modifier>(this.baseUrl + '/public/data/modifiers.tsv', {}, this.processObject.bind(this))
              .then((value) => {
                tempTargetsAndModifiers.modifiers = value as Modifier[];
                this.targetsAndModifiersData = tempTargetsAndModifiers;
                return callback(this.targetsAndModifiersData);
              }).catch((reason) =>{
              console.log(JSON.stringify(reason));
            })

          }).catch((reason) => {
          console.log(JSON.stringify(reason));
        });
      } else {
        return callback(this.targetsAndModifiersData);
      }

  }
}

