import { Injectable } from '@angular/core';

import { Target, Modifier, TargetsAndModifiers } from './data.service';
import {isUndefined} from 'util';

export interface PlainTextChunk {
  text: string;
  type: 'plainTextChunk';
  polarity: 'none';
}

export interface ModifierTextChunk {
  text: string;
  id: number; // Corresponds to Modifier.id
  type: 'modifierTextChunk';
  polarity: 'pos' | 'neg';
}

export interface TargetTextChunk {
  text: string;
  id: number; // Corresponds to Target.id
  type: 'targetTextChunk';
  polarity: 'none';
}

export type ParsedInputSentenceData = (PlainTextChunk | ModifierTextChunk | TargetTextChunk)[][];

interface Span {
  start: number;
  end: number;
}

interface TargetSpan extends Span {
  type: 'targetSpan';
  id: number;
  polarity: 'none';
}

interface ModifierSpan extends Span {
  type: 'modifierSpan';
  id: number;
  polarity: 'pos' | 'neg';
}

interface WordSpans {
  words: string[];
  spans: Span[];
}

interface TargetAndModifierSpans { // One TargetAndModifierSpan object represents targets and modifiers for one sentence.
  targetSpans: TargetSpan[];
  modifierSpans: ModifierSpan[];
}

@Injectable()
export class RbSentenceParserService {

  constructor() { }

  public parseInputSentences(inputSentences: string[], targetsAndModifiers: TargetsAndModifiers): ParsedInputSentenceData {
    const cleanedSentences: string[] = this.cleanWhitespace(inputSentences);
    const wordSpans: WordSpans[] = this.findWordSpans(cleanedSentences);
    const targetAndModSpans: TargetAndModifierSpans[] = this.findTargetAndModifierSpans(cleanedSentences, targetsAndModifiers);
    const textChunks: ParsedInputSentenceData = this.createTextChunks(wordSpans, targetAndModSpans);
    return textChunks;

  }

  private cleanWhitespace(inputSentences: string[]): string[] {
    const cleanedSentences = inputSentences.map((rawSentence: string) => {
      const cleanSentence = rawSentence.trim().replace(/\s+/, ' ');
      return cleanSentence;
    });
    return cleanedSentences;
  }

  private findWordSpans(cleanedSentences: string[]): WordSpans[] {
    const allWordSpans: WordSpans[] = cleanedSentences.map((cleanSentence: string) => {
      const regex = RegExp(/\S+/, 'g');
      const wordmatches = [];
      let match;
      while ((match = regex.exec(cleanSentence)) !== null) {
        wordmatches.push(match);
      }

      const sentenceWordSpans: Span[] = [];
      const sentenceWords: string[] = [];
      for (let i = 0; i < wordmatches.length; i++) {
        let currentStart: number;
        let currentEnd: number;
        // Take span as position of first character to position of last character.
        currentStart = wordmatches[i].index;
        currentEnd = wordmatches[i].index + wordmatches[i][0].length - 1;


        const span: Span = {start: currentStart, end: currentEnd};
        sentenceWordSpans.push(span);

        const currentWord = wordmatches[i][0];
        sentenceWords.push(currentWord);
      }

      return {words: sentenceWords, spans: sentenceWordSpans};
    });

    return allWordSpans;
  }

  // private getSpansFromMatches(matchObj: any):

  private findTargetAndModifierSpans(cleanedSentences: string[], targetsAndModifiers: TargetsAndModifiers):
  TargetAndModifierSpans[] {
    const allTargetAndModifierSpans: TargetAndModifierSpans[] = [];
    for (const cleanSentence of cleanedSentences) {

      const targetSpans: TargetSpan[] = [];
      for (const target of targetsAndModifiers.targets) {
        const regex = RegExp(target.regex, 'gi');
        const sentenceTargetSpans = this.findRegexSpans(cleanSentence, regex, 'targetSpan', target.id, targetsAndModifiers);
        for (const singleTargetSpan of sentenceTargetSpans) {
          targetSpans.push(singleTargetSpan as TargetSpan);
        }
      }

      const modifierSpans: ModifierSpan[] = [];
      for (const modifier of targetsAndModifiers.modifiers) {
        const regex = RegExp(modifier.regex, 'gi');
        const sentenceModifierSpans = this.findRegexSpans(cleanSentence, regex, 'modifierSpan', modifier.id, targetsAndModifiers);
        for (const singleModifierSpan of sentenceModifierSpans) {
          modifierSpans.push(singleModifierSpan as ModifierSpan);
        }
      }

      allTargetAndModifierSpans.push({targetSpans, modifierSpans})
    }

    return allTargetAndModifierSpans;
  };

  private findRegexSpans(text: string,
                         regex: RegExp,
                         type: 'targetSpan' | 'modifierSpan',
                         id: number,
                         targetsAndModifiers: TargetsAndModifiers): TargetSpan[] | ModifierSpan[] {
    const matches = [];
    let notNull = true;
    let match;
    while ((match = regex.exec(text)) !== null) {
      matches.push(match);
    }

    const spans: any[] = [];
    for (const match of matches) {
      const start: number = match.index;
      const end: number = start + match[0].length;
      spans.push({start, end, type, id});
    }

    if (type === 'targetSpan') {
      // Add the polarity for the target spans, which is always none. The polarity is used by the ui components.
      for (let span of spans) {
        span.polarity = 'none';
      }
      return spans as TargetSpan[];
    } else {
      // Add the polarity to each of the spans.
      for (let span of spans) {
        const mySpan = span as ModifierSpan;
        const modifier = this.findTargetOrModifierByID(mySpan.id, targetsAndModifiers);
        const spanType = modifier.type === 'AFFIRMED_EXISTENCE' ? 'pos' : 'neg';
        span.polarity = spanType;
      }
      return spans as ModifierSpan[];
    }
  }

  private createTextChunks(wordSpans: WordSpans[],
                           targetAndModifierSpans: TargetAndModifierSpans[],
                           filterModifiersWithoutTargets: boolean=true):
  ParsedInputSentenceData {
    if (wordSpans.length !== targetAndModifierSpans.length) {
      throw Error("Word spans and targetAndModifierSpans should have the same length.");
    }
    const parsedSentenceChunks: ParsedInputSentenceData = [];
    for (let sentenceIndex = 0; sentenceIndex < wordSpans.length; sentenceIndex++) {
      // These values represent the word spans, modifier spans, and target spans for a single sentence.
      const currentWordSpans = wordSpans[sentenceIndex].spans;
      const currentWords = wordSpans[sentenceIndex].words;
      const currentModifierSpans = targetAndModifierSpans[sentenceIndex].modifierSpans;
      const currentTargetSpans = targetAndModifierSpans[sentenceIndex]. targetSpans;
      const currentSentence = currentWords.join(' ');


      // Create an object that maps from string index to word index, assuming that whitespace belongs to the
      // word that follows it. Then create a list that combines all target and modifier spans, sorted by
      // start, assuming that none of them overlap, which they shouldn't.
      // Then simply iterate through the list, using the index mapper to determine where each target or
      // modifier begins and ends.

      const sentenceIndexToWordIndexMap = this.createStringIndexToWordIndexMap(currentSentence, currentWordSpans);
      const combinedSortedList = this.createSortedListOfTargetsAndModifierSpans(currentTargetSpans, currentModifierSpans);
      const currentTargetAndModifierWordSpans = this.convertTargetAndModifierSentenceSpansToWordSpans(combinedSortedList, sentenceIndexToWordIndexMap);


      // Word index where the last span ended. It is from this index+1 up to the next index-1 that we will grab.
      // If (startOfNextSpan - 1) - (endOfLastSpan + 1) <= 0 then we insert no plain chunk, we go right from
      // one highlight to another.
      const chunksForThisSentence = []

      // Start with the easy case: there are no targets or modifiers.
      if (currentModifierSpans.length === 0 && currentTargetSpans.length === 0) {

        chunksForThisSentence.push({text: currentSentence, type: 'plainTextChunk', polarity: 'none'})

        // The next easiest case is if `filterModifiersWithoutTargets` is true and the sentence has no targets
      } else if (filterModifiersWithoutTargets && currentTargetSpans.length === 0) {

        chunksForThisSentence.push({text: currentSentence, type: 'plainTextChunk', polarity: 'none'})

      } else {

        let endOfLastSpan = -1;
        for (const targetModSpan of currentTargetAndModifierWordSpans) {
          let chunkType = '';
          let text = '';
          let highlightChunk = null;
          let plainChunk = null;
          if ((targetModSpan.start - 1) - endOfLastSpan <= 0) { // i.e. Next span starts at next word
            // Skip adding a plain chunk and just add a chunk for the next span.
            chunkType = targetModSpan.type === 'targetSpan' ? 'targetTextChunk': 'modifierTextChunk';
            text = this.extractWordsFromSpan(currentWords, targetModSpan);
            highlightChunk = {text, id: targetModSpan.id, type: chunkType, polarity: targetModSpan.polarity};
            chunksForThisSentence.push(highlightChunk);
            endOfLastSpan = targetModSpan.end;
          } else {
            // Add a plain chunk up to the next span and then add the next span.
            chunkType = 'plainTextChunk';
            text = this.extractWordsFromSpan(currentWords, {start: (endOfLastSpan + 1), end: (targetModSpan.start - 1)});
            plainChunk = {text, type: chunkType, polarity: 'none'};
            chunksForThisSentence.push(plainChunk);

            // Add next span
            chunkType = targetModSpan.type === 'targetSpan' ? 'targetTextChunk': 'modifierTextChunk';
            text = this.extractWordsFromSpan(currentWords, targetModSpan);
            highlightChunk = {text, id: targetModSpan.id, type: chunkType, polarity: targetModSpan.polarity};
            chunksForThisSentence.push(highlightChunk);
            endOfLastSpan = targetModSpan.end;
          }
        }
      }

      parsedSentenceChunks.push(chunksForThisSentence);

    }
    return parsedSentenceChunks;
  };


  private createStringIndexToWordIndexMap(sentence: string, wordSpans: Span[]): number[] {
    let currentWordIndex = 0;
    const mapper = [];
    for (let sentenceIndex = 0; sentenceIndex < sentence.length; sentenceIndex++) {
      const currentWordSpan = wordSpans[currentWordIndex];
      mapper.push(currentWordIndex);
      if (sentenceIndex > currentWordSpan.end) {
        currentWordIndex++;
      }
    }
    return mapper;
  }

  private createSortedListOfTargetsAndModifierSpans(targetSpans: TargetSpan[], modifierSpans: ModifierSpan[]) {
    let targetCopy: (TargetSpan | ModifierSpan)[] = [];
    targetCopy.push(...targetSpans);
    let combinedList: (TargetSpan | ModifierSpan)[] = targetCopy.concat(modifierSpans);
    combinedList.sort((span1, span2) => {
      return span1.start - span2.start;
    });
    return combinedList;
  }

  private convertTargetAndModifierSentenceSpansToWordSpans(sentenceSpans: (TargetSpan | ModifierSpan)[],
                                                           wordMap: number[]): (TargetSpan | ModifierSpan)[] {
    const convertedSpans: any = [];

    for (const span of sentenceSpans) {
      const startWord = wordMap[span.start];
      let endWord = wordMap[span.end];
      if (span.end >= wordMap.length) {
        endWord = wordMap[wordMap.length - 1];
      }
      convertedSpans.push({start: startWord, end: endWord, id: span.id, type: span.type, polarity: span.polarity})
    }
    return convertedSpans
  }

  private extractWordsFromSpan(words: string[], span: Span) {
    const extractedWords =[];
    for (let i = span.start; i <= span.end; i++) {
      extractedWords.push(words[i]);
    }
    return extractedWords.join(' ');
  }

  private findTargetOrModifierByID(id: number, targetsAndModifiers: TargetsAndModifiers){
    for (let target of targetsAndModifiers.targets) {
      if (target.id === id){
        return target;
      }
    }
    for (let modifier of targetsAndModifiers.modifiers) {
      if (modifier.id === id){
        return modifier;
      }
    }
  }

}



