import { Component, OnInit, Input, AfterViewInit, ViewEncapsulation } from '@angular/core';

import * as d3 from 'd3-selection';

@Component({
  encapsulation: ViewEncapsulation.None,
  selector: 'app-explanation-text-box',
  template: `
    <div id="rb-explanation">
      <div id="arrow-title-div">
        <svg width="25px" height="25px" (click)="onSvgClick()" id="explanation-text-box-svg">
          <g id="explanation-text-box-left-arrow-group" transform="translate(7, 3)">
            <polyline id="left-arrow" points="9.71 18.49 1.37 9.59 9.71 0.68"/>
          </g>
        </svg>
        <span id="explanation-span">Explanation</span>
      </div>
      <div id="explanation-text-content-div" class="closed" [innerHTML]="content"></div>
    </div>
  `,
  styleUrls: ['./explanation-text-box.component.css']
})
export class ExplanationTextBoxComponent implements OnInit {

  @Input() content: string;

  constructor() {}

  ngOnInit() {

  }

  onSvgClick() {
    const svg = d3.select('#explanation-text-box-svg');
    svg.classed('open', !svg.classed('open'));

    const contentDiv = d3.select('#explanation-text-content-div');
    contentDiv.classed('closed', !contentDiv.classed('closed'));

    const explanationSpan = d3.select('#explanation-span');
    explanationSpan.classed('hidden', !explanationSpan.classed('hidden'));
  }
}



